import React, { Component } from 'react'
import {
    Card,
    CardBody,
    FormGroup,
    Label,
    Input,
    Row,
    Col,
    CustomInput,
    Button
} from "reactstrap"
export default class AddNeUniBoard extends Component {
    render() {
        return (
            <Card>
                <CardBody>
                    <form>
                        <Row>
                            <Col lg="6" md="6" sm="12">
                                <FormGroup>
                                    <Label for="">Date</Label>
                                    <Input type="date" id="" placeholder="Enter Calender" required />
                                </FormGroup>
                            </Col>
                            <Col lg="6" md="6" sm="12">
                                <FormGroup>
                                    <Label for="">Uni/Board Name</Label>
                                    <Input type="text" id="" placeholder="Enter Uni/Board Name" required />
                                </FormGroup>
                            </Col>
                            <Col lg="6" md="6" sm="12">
                                <FormGroup>
                                    <Label for="customFile">Logo</Label>
                                    <CustomInput
                                        type="file"
                                        id="exampleCustomFileBrowser"
                                        name="customFile"
                                        required />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Button.Ripple color="primary" type="submit">Submit </Button.Ripple>
                    </form>
                </CardBody>
            </Card>
        )
    }
}
